.table {
    .cellWrapper {
      display: flex;
      align-items: center;
  
      .image {
        width: 240px;
        height: 200px;
        border: none;
        margin-right: 10px;
        
      } 
      
    }
  
    .status {
      padding: 5px;
      border-radius: 5px;
  
      &.Approved {
        color: green;
        background-color: rgba(0, 128, 0, 0.151);
      }
      &.Pending {
        color: goldenrod;
        background-color: rgba(189, 189, 3, 0.103);
      }
    }
    .imageOverlay {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: rgba(0, 0, 0, 0.7);
      z-index: 9999;
    }
    
    .close {
      position: absolute;
      top: 10px;
      right: 10px;
      font-size: 24px;
      color: #fff;
      cursor: pointer;
    }
    
    .fullImage {
      width: 50%;
      max-height: 100%;
    }
    
  }
  